import React, { Component } from 'react';
import styled from 'styled-components';

const ImageWrapper = styled.div`
	border-radius: 24px;
	position: relative;
	overflow: hidden;
	img {
		border-radius: inherit;
		max-width: 100%;
		max-height: 100%;
		margin: 10px 0px;
	}
	.answer_overlay {
		transition: 300ms ease;
		position: absolute;
		width: 100%;
		height: calc(100% - 10px);
		background: radial-gradient(rgba(0, 0, 0, 0.7) 20%, black);
		border-radius: inherit;
		left: 100%;
		top: 0;
		.answer {
			padding: 40px;
			width: calc(100% - 80px);
			height: calc(100% - 80px);
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			span {
				font-size: 40px;
				text-align: center;
				text-transform: uppercase;
			}
		}
		&.show {
			left: 0%;
		}
	}
`;

class callout_image_box extends Component {
	state = {
		current_index: 0,
		activeMap: ''
	};

	render() {
		return (
			<ImageWrapper>
				<img src={this.props.activeImage.image_path} alt="" />
				<span
					className={
						this.props.timer === 0 && this.props.timerIsPaused
							? 'answer_overlay show'
							: 'answer_overlay'
					}
				>
					<span className="answer">
						<span>
							<p>Correct Answer</p>{' '}
							<p>{this.props.activeImage.callout_location}</p>
						</span>
					</span>
				</span>
			</ImageWrapper>
		);
	}
}

export default callout_image_box;
